function ContactForm() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [subject, setSubject] = useState("");
  const [message, setMessage] = useState("");

  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const to = "hello@example.com";
    const safeSubject = encodeURIComponent(subject || `Message from ${name || "your site"}`);
    const bodyLines = [
      name ? `Name: ${name}` : undefined,
      email ? `Email: ${email}` : undefined,
      "",
      message || "",
    ].filter(Boolean);
    const body = encodeURIComponent(bodyLines.join("\n"));
    window.location.href = `mailto:${to}?subject=${safeSubject}&body=${body}`;
  };

  return (
    <form onSubmit={onSubmit} className="hover-gradient-card rounded-2xl bg-white/5 ring-1 ring-white/10 p-6 sm:p-8 shadow">
      <h2 className="text-xl sm:text-2xl font-bold tracking-tight mb-4">Send a Message</h2>

      <div className="space-y-4">
        <div>
          <label htmlFor="name" className="block text-sm font-medium text-foreground/80">Name</label>
          <input
            id="name"
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="mt-1 w-full rounded-md bg-white/15 backdrop-blur-md ring-1 ring-white/20 px-3 py-2 text-white placeholder-white/70 shadow-inner outline-none focus:bg-white/20 focus:ring-2 focus:ring-cyan-400/60"
            placeholder="Your name"
            autoComplete="name"
          />
        </div>

        <div>
          <label htmlFor="email" className="block text-sm font-medium text-foreground/80">Email</label>
          <input
            id="email"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="mt-1 w-full rounded-md bg-white/15 backdrop-blur-md ring-1 ring-white/20 px-3 py-2 text-white placeholder-white/70 shadow-inner outline-none focus:bg-white/20 focus:ring-2 focus:ring-cyan-400/60"
            placeholder="you@example.com"
            autoComplete="email"
            required
          />
        </div>

        <div>
          <label htmlFor="subject" className="block text-sm font-medium text-foreground/80">Subject</label>
          <input
            id="subject"
            type="text"
            value={subject}
            onChange={(e) => setSubject(e.target.value)}
            className="mt-1 w-full rounded-md bg-white/15 backdrop-blur-md ring-1 ring-white/20 px-3 py-2 text-white placeholder-white/70 shadow-inner outline-none focus:bg-white/20 focus:ring-2 focus:ring-cyan-400/60"
            placeholder="How can I help?"
          />
        </div>

        <div>
          <label htmlFor="message" className="block text-sm font-medium text-foreground/80">Message</label>
          <textarea
            id="message"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            className="mt-1 w-full rounded-md bg-white/15 backdrop-blur-md ring-1 ring-white/20 px-3 py-2 h-32 resize-y text-white placeholder-white/70 shadow-inner outline-none focus:bg-white/20 focus:ring-2 focus:ring-cyan-400/60"
            placeholder="Tell me a bit about your project..."
            required
          />
        </div>
      </div>

      <button
        type="submit"
        className="mt-5 w-full rounded-md bg-cyan-600/80 hover:bg-cyan-600 text-white px-4 py-2.5 text-sm font-medium ring-1 ring-cyan-500/40 transition"
      >
        Send Message
      </button>
    </form>
  );
}
