"use client";

import React, { useRef, useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// SplitText is a paid GSAP plugin. We'll try to load it if available.
let SplitTextPlugin: any = null;
try {
  // eslint-disable-next-line @typescript-eslint/no-var-requires
  SplitTextPlugin = require("gsap/SplitText").SplitText || require("gsap/SplitText");
  if (SplitTextPlugin) {
    gsap.registerPlugin(SplitTextPlugin);
  }
} catch (e) {
  // Plugin not available; component will gracefully degrade without animation.
}

// Register ScrollTrigger (free plugin, safe to import)
gsap.registerPlugin(ScrollTrigger);

export type SplitTextProps = {
  text: string;
  className?: string;
  delay?: number; // milliseconds between characters
  duration?: number;
  ease?: string;
  splitType?: "chars" | "words" | "lines";
  from?: gsap.TweenVars;
  to?: gsap.TweenVars;
  threshold?: number; // 0..1 portion of element in view before start
  rootMargin?: string; // like CSS margin (e.g. "-100px")
  textAlign?: React.CSSProperties["textAlign"];
  onLetterAnimationComplete?: () => void;
};

const SplitText: React.FC<SplitTextProps> = ({
  text,
  className = "",
  delay = 100,
  duration = 0.6,
  ease = "power3.out",
  splitType = "chars",
  from = { opacity: 0, y: 40 },
  to = { opacity: 1, y: 0 },
  threshold = 0.1,
  rootMargin = "-100px",
  textAlign = "center",
  onLetterAnimationComplete,
}) => {
  const ref = useRef<HTMLParagraphElement | null>(null);
  const animationCompletedRef = useRef(false);
  const scrollTriggerRef = useRef<ScrollTrigger | null>(null);

  useEffect(() => {
    if (typeof window === "undefined" || !ref.current || !text) return;

    const el = ref.current;
    animationCompletedRef.current = false;

    const absoluteLines = splitType === "lines";
    if (absoluteLines) (el as HTMLElement).style.position = "relative";

    let splitter: any;
    try {
      if (!SplitTextPlugin) {
        // If plugin missing, simply set final state and exit.
        gsap.set(el, { opacity: to?.opacity ?? 1, y: to?.y ?? 0 });
        return;
      }
      // eslint-disable-next-line new-cap
      splitter = new SplitTextPlugin(el, {
        type: splitType,
        absolute: absoluteLines,
        linesClass: "split-line",
      });
    } catch (error) {
      console.error("Failed to create SplitText:", error);
      return;
    }

    let targets: HTMLElement[] = [];
    switch (splitType) {
      case "lines":
        targets = splitter.lines as HTMLElement[];
        break;
      case "words":
        targets = splitter.words as HTMLElement[];
        break;
      case "chars":
      default:
        targets = splitter.chars as HTMLElement[];
        break;
    }

    if (!targets || targets.length === 0) {
      console.warn("No targets found for SplitText animation");
      splitter.revert();
      return;
    }

    targets.forEach((t: HTMLElement) => {
      t.style.willChange = "transform, opacity";
    });

    // Build ScrollTrigger start value based on threshold and rootMargin
    const startPct = (1 - threshold) * 100;
    const marginMatch = /^(\-?\d+(?:\.\d+)?)(px|em|rem|%)?$/.exec(rootMargin);
    const marginValue = marginMatch ? parseFloat(marginMatch[1]) : 0;
    const marginUnit = marginMatch ? marginMatch[2] || "px" : "px";
    const sign = marginValue < 0 ? `-=${Math.abs(marginValue)}${marginUnit}` : `+=${marginValue}${marginUnit}`;
    const start = `top ${startPct}%${sign}`;

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: el,
        start,
        toggleActions: "play none none none",
        once: true,
        onToggle: (self) => {
          scrollTriggerRef.current = self as unknown as ScrollTrigger;
        },
      },
      smoothChildTiming: true,
      onComplete: () => {
        animationCompletedRef.current = true;
        gsap.set(targets, {
          ...to,
          clearProps: "willChange",
          immediateRender: true,
        });
        onLetterAnimationComplete?.();
      },
    });

    tl.set(targets, { ...from, immediateRender: false, force3D: true });
    tl.to(targets, {
      ...to,
      duration,
      ease,
      stagger: delay / 1000,
      force3D: true,
    });

    return () => {
      tl.kill();
      if (scrollTriggerRef.current) {
        scrollTriggerRef.current.kill();
        scrollTriggerRef.current = null;
      }
      gsap.killTweensOf(targets);
      try { splitter && splitter.revert && splitter.revert(); } catch {}
    };
  }, [
    text,
    delay,
    duration,
    ease,
    splitType,
    from,
    to,
    threshold,
    rootMargin,
    onLetterAnimationComplete,
  ]);

  return (
    <p
      ref={ref}
      className={`split-parent overflow-hidden inline-block whitespace-normal ${className}`}
      style={{ textAlign, wordWrap: "break-word" }}
    >
      {text}
    </p>
  );
};

export default SplitText;
